import "./App.css";
import { useState, useEffect } from "react";
import { getCode } from "./Blackbox";
import axios from 'axios';
import CodeMirror from "@uiw/react-codemirror";
import { javascript } from '@codemirror/lang-javascript';

const langs_txt = require("./langs.txt");

export default function App(): any {
  const [response, setResponse] = useState('');
  const [langs, setLangs] = useState([]);
  const [input, setInput] = useState("Ada");
  const [code, setCode] = useState('');
  const [output, setOutput] = useState("Ada");
  const [thinking, setThinking] = useState('');

  useEffect(() => {
    axios.get(langs_txt).then(response => setLangs(response.data.split("\n")));
  }, []);

  const InputLanguageSelect = () => {
    return (
      <select value={input} onChange={(element) => setInput(element.target.value)}>
        {langs.map((lang, key) => (
          <option key={key}>{lang}</option>
        ))}
      </select>
    );
  };

  const OutputLanguageSelect = () => {
    return (
      <select value={output} onChange={(element) => setOutput(element.target.value)}>
        {langs.map((lang, key) => (
          <option key={key}>{lang}</option>
        ))}
      </select>
    );
  };

  const submit = () => {
    setThinking("thinking...");
    getCode(output, input, code).then((result) => {
      setResponse(result);
      setThinking('');
    });
  }

  return (
    <div id="main">
      <center>
        <div id="header">
          <h1>AI Code Translator</h1>
          <p>Translate your code to other language using Blackbox AI</p>
        </div>
      </center>
      <div id="editors">
        <div id="input">
          <p>
            Input
            <InputLanguageSelect />
          </p>
          <CodeMirror onChange={(string) => setCode(string)} width="400px" height="400px" theme="dark" 
              extensions={[javascript({ jsx: true })]} />
        </div>
        <div id="output">
          <p>
            Output
            <OutputLanguageSelect />
          </p>
          <CodeMirror value={response} width="400px" height="400px" theme="dark" 
              extensions={[javascript({ jsx: true })]} />
        </div>
      </div>
      <center>
        <div id="translate">
          <button onClick={submit}>Translate</button>
          <p>{thinking}</p>
        </div>
        <div id="footer">
          <p className="footer-p">
            made by
            <a href="https://github.com/strokegmd"> strokegmd </a>
            //<a href="https://github.com/strokegmd/strokegmd.github.io"> view on github </a>
            // version v1.1</p>
        </div>
      </center>
    </div>
  );
}
