import axios from 'axios';
import { AxiosResponse } from 'axios';

const proxy_base_url = "https://satin-obsidian-promise.glitch.me/";
const blackbox_base_url = proxy_base_url + "https://blackbox.ai/api/chat";

const json_base_data = {
    "messages": [
        {
            "content": '',
            "role": "user"
        }
    ],
    "agentMode": {},
    "trendingAgentMode": {}
};

const newline = String.fromCharCode(13, 10);

const prettifyData = (response: AxiosResponse) => {
    const data = JSON.stringify(response.data);
    const no_version = data.search("undefined") > 0 ? data.substring(22) : data.substring(18);

    let code_block = no_version.split("```")[1];
    if (!code_block) code_block = no_version;

    const fixed_newlines = code_block.replaceAll("\\n", newline);
    const fixed_asm = fixed_newlines.replaceAll("section.", "section .");
    const prettified_data = fixed_asm.replaceAll('\\"', '"').replaceAll("\\'", "'");
    const start = prettified_data.startsWith(newline) ? 2 : 0;
    return prettified_data.substring(start, prettified_data.length - 1);
};

const getPrompt = (to: string, from: string, code: string) => {
    const prompt = `Hello, your task is to translate this code from ${from} to ${to}. \
Do not create any comments or something. You need only to give me the code. \
In code block, don't specify language. Send me just plain text. \
${code}`;
    
    return prompt;
};

export const getCode = async (to: string, from: string, code: string) => {
    json_base_data["messages"][0]["content"] = getPrompt(to, from, code);
    const response = await axios.post(blackbox_base_url, json_base_data);
    const data = prettifyData(response);

    return data;
};